body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 960px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: 1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
}

* {
  line-height: 1.7;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: .5rem;
  font-family: inherit;
  font-weight: 500;
  line-height: 1.2;
  color: inherit;
}

.top-nav {
  width: 100%;
  background-color: #ff0;
  padding: 10px 0;
}

.top-bar {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-between;
}

.top-bar ul {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
}

.top-bar ul li {
  display: inline;
}

.top-menu-nav {
  display: flex;
  justify-content: space-between;
}

.top-menu-nav ul li {
  display: inline;
  padding-left: 20px;
}

.top-menu {
  width: 100%;
  background-color: antiquewhite;
  padding: 30px 0;
}

section {
  padding: 100px 0;
}

.product {
  width: 100%;
  background-color: rgba(255, 255, 255, 0.959);
}

.wrapper-bkg {
  width: 100%;
  background-color: #f7db5e;
}

.products {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  text-align: center;
  margin-top: 50px;
}

.products li {
  border: #ff0 1px solid;
  margin-bottom: 10px;
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(20% - 10px);
}

.advantage {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  text-align: center;
  margin-top: 50px;
}

.advantage li {
  margin-bottom: 10px;
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 10px);
}

.stages-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  text-align: center;
  margin-top: 50px;
}

.stages-list li {
  margin-bottom: 10px;
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(25% - 10px);
}

.footer-bkg {
  width: 100%;
  padding: 50px 0;
  background-color: antiquewhite;
}

.footer-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

.footer-list div {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 10px);
  display: flex;
  flex-direction: column;
  justify-self: flex-end;
}

.footer-list div p:first-child {
  padding: 0 0 40px 0;
}

.footer-link {
  display: flex;
}

.footer-link ul {
  margin: 0 0 0 auto;
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .products li {
    width: calc(25% - 10px);
  }

  .advantage li {
    width: calc(33.333333333333336% - 10px);
  }

  .stages-list li {
    width: calc(25% - 10px);
  }

  .footer-list div {
    width: calc(33.333333333333336% - 10px);
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .products li {
    width: calc(50% - 10px);
  }

  .advantage li {
    width: calc(50% - 10px);
  }

  .stages-list li {
    width: calc(50% - 10px);
  }

  .footer-list div {
    width: calc(50% - 10px);
  }

  .footer-link ul {
    padding: 40px 0 0 0;
    margin: 0 0 0 0;
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .products li {
    width: calc(100% - 10px);
  }

  .advantage li {
    width: calc(100% - 10px);
  }

  .stages-list li {
    width: calc(100% - 10px);
  }

  .footer-list div {
    width: calc(100% - 10px);
  }
}